@import url(https://fonts.googleapis.com/css2?family=Imbue:wght@400;600&family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html:focus-within{
  scroll-behavior: smooth;
}
.App {
  text-align: center;
  background-color: #FBC403;
}


.App-header {
  background-color: #282c34;
  height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Imbue', serif;
  font-size: calc(10px + 2vmin);
  font-weight: 600;
  color: white;
  padding: 20px;
  cursor: pointer;
}

.spinner {
  height: 80px;
  margin: auto;
}

.App-intro{
  font-size: large;
}

/* new css for movie component */

*{
    box-sizing: border-box;
}

.movies{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px,1fr));
}

.App-header h2{
    margin: 0;
}

.add-movies{
    text-align: center;
}

.add-movies button{
    font-size: 16px;
    padding: 8px;
    max-width: 25%;
}

.movie{
    justify-content: center;
    align-items: center;
    padding: 5px 25px 10px 25px;
    /* max-width: 25%; */
    position: relative;
    transition: transform 0.2s;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
}
.movie h2{
  font-size: 1em;
}
.movie img{
  object-fit: cover;
}
.movie:hover{
  background-color: #151515;
  color: #fff;
  cursor: pointer;
  transform: scale(1);
}
.movie:hover .overlay{
  opacity: 1;
}
.overlay{
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  margin: 0 auto;
  width: calc(100% - 65px);
  transition: 0.5s;
  opacity: 0;
  bottom: 0;
  font-size: 20px;
  padding: 20px;
  text-align: center;
  color: #fff;
}
.overlay svg{
  height: 1em;
  width: 1em;
  margin-left: 5px;
}
.movies.nominated .overlay{
  font-size: 18px;
  padding: 20px;
}
.errorMessage{
    margin: auto;
    font-weight: bold;
    color: rgb(161, 15, 15);
}

.search{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 10px;
}
.popup{
  text-align: center;
}

input[type="submit"] {
    padding: 5px;
    background-color: transparent;
    color: black;
    border: 1px solid black;
    width: 80px;
    margin-left: 5px;
    cursor: pointer;
}
input[type="submit"]:hover{
    background-color: #282c34;
    color: antiquewhite;
}

.search > input[type="text"] {
    width: 40%;
    min-width: 170px;
}


@media(prefers-reduced-motion) {
  html:focus-within{
    scroll-behavior: auto;
  }
}
